'use client';
import { useState } from 'react';
import { motion } from 'framer-motion';

import styles from '../styles';
import { exploreWorlds } from '../constants';
import { staggerContainer } from '../utils/motion';
import { ExploreCard, TitleText, TypingText } from '../components';
function Explore() {
 
  const [active, setActive] = useState('world-2');

  return<>

 

   <section className={`${styles.paddings} ` } id='explore'>
   <motion.div variants={staggerContainer}  initial="hidden" whileInView="show" viewport={{once:false , amount:0.25}} className={`${styles.innerWidth} m-auto flex flex-col `} >
     
     <TypingText title="|the world " textstyles="text-center"/>
     <TitleText title={<>choose the world you want<br  className='md:block hidden' /> to explore
      </>} textstyles="text-center"/>

     <div className='mt-[50px] flex lg:flex-row flex-col min-h-[70vh] gap-5'>

       {exploreWorlds.map((world, index)=>(
         <ExploreCard
         {...world}
         key={world.id}
         index={index}
         active={active}
         handleClick={setActive}
         />
       ))}

     </div>
     </motion.div> 

 </section>
   
  </>
 
};

export default Explore;
